<template>
  <div class="home-wrap">
    <!-- Badge horario -->
    <div
      :class="['alert', abierto ? 'alert-success' : 'alert-secondary', 'text-center', 'py-2', 'mb-0']"
      role="alert"
    >
      {{ abierto ? 'ABIERTO' : 'CERRADO' }} • {{ horario.open }}–{{ horario.close }} (Llallagua)
    </div>

    <!-- Centro -->
    <section class="hero-wrap">
      <div class="container cta-zone" style="max-width:900px;">
        <img :src="base + 'img/escudo.png'" alt="Pollos Dumbo" class="logo" />
        <h1 class="brand">Pollos Dumbo</h1>
        <p class="subtitle">¡El mejor pollo de Llallagua!</p>
        <p class="copy">
          Abrimos de <strong>3:00 pm</strong> a <strong>9:00 pm</strong>. Pide para delivery o pasa a recoger.
        </p>

        <div class="d-flex justify-content-center gap-3 flex-wrap mt-3">
          <RouterLink :to="{ name: 'pollo' }" class="btn btn-brand btn-lg px-4">Ver menú</RouterLink>
          <RouterLink :to="{ name: 'pedido' }" class="btn btn-outline-dark btn-lg px-4">Hacer pedido</RouterLink>
        </div>
      </div>
    </section>

    <footer class="text-center py-3">
      © {{ year }} Pollos Dumbo • Llallagua
    </footer>
  </div>
</template>

<script>
export default {
  name: "HomeView",
  data() {
    return {
      base: process.env.BASE_URL,
      year: new Date().getFullYear(),
      horario: { open: "15:00", close: "21:00" },
      nowMins: this.getNowMins(),
    };
  },
  computed: {
    abierto() {
      const toMin = (s) => { const [h, m] = s.split(":").map(Number); return h * 60 + m; };
      return this.nowMins >= toMin(this.horario.open) && this.nowMins <= toMin(this.horario.close);
    },
  },
  mounted() {
    // Actualiza badge cada minuto
    this._tick = setInterval(() => (this.nowMins = this.getNowMins()), 60_000);
  },
  beforeUnmount() {
    clearInterval(this._tick);
  },
  methods: {
    getNowMins() {
      const n = new Date();
      return n.getHours() * 60 + n.getMinutes();
    },
  },
};
</script>

<style scoped>
/* Hereda paleta desde global.css; sólo detalles del home */
.home-wrap {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.hero-wrap {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 32px 16px;
}

.brand {
  font-family: "Luckiest Guy", cursive;
  letter-spacing: 1px;
  font-size: clamp(32px, 6vw, 56px);
  margin: 16px 0 12px;
}

.subtitle {
  font-weight: 800;
  text-transform: uppercase;
  font-size: clamp(16px, 2.5vw, 22px);
  margin-bottom: 8px;
}

.copy {
  font-weight: 600;
  margin-bottom: 20px;
}

.btn-brand {
  background: var(--brand-accent);
  border: none;
  border-radius: 10px;
  font-weight: 700;
  color: #fff;
}
.btn-brand:hover { background: #e63900; }

.logo {
  width: clamp(120px, 22vw, 180px);
  height: auto;
  display: block;
  margin: 0 auto 8px;
  filter: drop-shadow(0 4px 12px rgba(0,0,0,.15));
}
</style>
