<template>
  <div class="container py-4">
    <h2 class="mb-3">Pedido de Pollos Dumbo</h2>

    <form @submit.prevent="submitPedido">
      <div class="mb-3">
        <label for="nombre" class="form-label">Nombre completo</label>
        <input v-model="pedido.nombre" id="nombre" type="text" class="form-control" required />
      </div>

      <div class="mb-3">
        <label for="direccion" class="form-label">Dirección</label>
        <input v-model="pedido.direccion" id="direccion" type="text" class="form-control" required />
      </div>

      <div class="mb-3">
        <label for="telefono" class="form-label">Teléfono</label>
        <input v-model="pedido.telefono" id="telefono" type="tel" class="form-control" required />
      </div>

      <div class="mb-3">
        <label class="form-label">Producto</label>
        <select v-model="pedido.producto" class="form-select" required>
          <option disabled value="">Seleccione...</option>
          <option value="entero">Pollo Entero</option>
          <option value="broaster">Pollo Broaster</option>
          <option value="frito">Pollo Frito Tradicional</option>
        </select>
      </div>

      <div class="mb-3">
        <label class="form-label">Porción</label>
        <select v-model="pedido.porcion" class="form-select">
          <option value="">Ninguna</option>
          <option value="arroz">Arroz</option>
          <option value="papa">Papa frita</option>
          <option value="fideo">Fideo</option>
          <option value="salsa">Salsa</option>
        </select>
      </div>

      <!-- Refresco -->
      <div class="mb-3">
        <label class="form-label">Refresco</label>
        <select v-model="pedido.refresco" class="form-select">
          <option value="">Ninguno</option>
          <option value="maracuya">Refresco de maracuyá</option>
          <option value="limonada">Limonada</option>
          <option value="gaseosa">Gaseosa</option>
        </select>
      </div>

      <!-- QR -->
      <div v-if="pedido.pago === 'qr'" class="mb-3 text-center">
        <img src="/img/QR.jpg" alt="QR" style="max-width:200px;" class="img-fluid rounded shadow" />
        <input v-model="pedido.qrRef" class="form-control mt-2" placeholder="Referencia (opcional)" />
      </div>

      <div class="mb-3">
        <label class="form-label">Método de pago</label>
        <div>
          <label><input type="radio" value="caja" v-model="pedido.pago" /> Caja</label>
          <label class="ms-3"><input type="radio" value="qr" v-model="pedido.pago" /> QR</label>
        </div>
      </div>

      <div class="alert alert-warning fw-bold">
        Total: Bs {{ total.toFixed(2) }}
      </div>

      <button type="submit" class="btn btn-primary">Enviar pedido</button>
    </form>
  </div>
</template>

<script>
import { onMounted, reactive, computed } from "vue";
import { useRoute } from "vue-router";
import CATALOGO from "@/data/catalogo";

export default {
  name: "PedidoView",
  setup() {
    const route = useRoute();

    const pedido = reactive({
      nombre: "",
      direccion: "",
      telefono: "",
      producto: route.query.producto || "",
      porcion: "",
      refresco: route.query.refresco || "",
      pago: "caja",
      qrRef: "",
    });

    const total = computed(() => {
      let t = 0;
      if (pedido.producto) t += CATALOGO.pollo[pedido.producto] || 0;
      if (pedido.porcion) t += CATALOGO.porciones[pedido.porcion] || 0;
      if (pedido.refresco && pedido.refresco !== "gaseosa") {
        t += CATALOGO.bebidas[pedido.refresco] || 0;
      }
      return t;
    });

    function submitPedido() {
      alert(`Pedido realizado por ${pedido.nombre}, Total Bs ${total.value}`);
    }

    onMounted(() => {
      console.log("Pedido precargado:", pedido);
    });

    return { pedido, total, submitPedido };
  },
};
</script>
